










// @ts-nocheck
import { Component, Prop, Vue } from 'vue-property-decorator';
import * as MapLibre from 'maplibre-gl';
import 'maplibre-gl/dist/mapbox-gl.css';
import Address from '../../public/data/address.json';
import BcFavorite from '../../public/data/bc_favorite.json';
import Bc from '../../public/data/bc.json';
import Hexagon from '../../public/data/hexagon.json';
import Isochrones from '../../public/data/isochrones.json';


@Component
export default class Map extends Vue {
  private readonly officeCoordinates: [number, number] = [30.291272, 59.953777];
  private readonly accessToken = 'pk.eyJ1IjoibWFrZW5vaXplciIsImEiOiJjazN1YWwzZmYwYXJ3M25xOG9jcjJ0bjZlIn0.ylHg0tCIXrvWRuB9ttVpOw';
  private map;
  private layersIds: Array<{ id: string, alias: string, isShown: boolean }> = [];

  private async mounted() {
    await this.initMap();
    this.loadResources();
    await this.loadImages();
    this.addIsochronesLayer();
    this.addHeatMap();
    this.addAddressesLayer();
    this.addBCLayer();
    this.addBCFavoriteLayer();
    this.addOfficeLayer();
  }

  private async initMap() {
    // @ts-ignore
    MapLibre.accessToken = this.accessToken;
    this.map = new MapLibre.Map({
      container: 'map', // container id
      style: 'mapbox://styles/makenoizer/cklc10v773ejb17nsv5is3bfg', // stylesheet location
      center: this.officeCoordinates, // starting position
      zoom: 15, // starting zoom
    });
    await this.waitMapLoad();
  }

  private waitMapLoad() {
    return new Promise((resolve => {
      this.map.on('load', () => {
        resolve();
      });
    }));
  }

  private onClick(id) {
    const targetLayer = this.layersIds.find((layer) => layer.id === id);
    targetLayer.isShown = !targetLayer.isShown;
    const visibility = targetLayer.isShown ? 'visible' : 'none';
    this.map.setLayoutProperty(id, 'visibility', visibility);
  }

  private loadResources() {
    const resources = [
      {
        name: 'isochrones_points',
        link: Hexagon, //isochrones_points
      },
      {
        name: 'isochrones',
        link: Isochrones,
      },
      {
        name: 'bc',
        link: Bc,
      },
      {
        name: 'bc_favorite',
        link: BcFavorite,
      },
      {
        name: 'address',
        link: Address,
      },
    ];
    for (const resource of resources) {
      this.map.addSource(resource.name, {
        type: 'geojson',
        data: resource.link,
      });
    }
    this.map.addSource('drn', {
      type: 'geojson',
      data: {
        type: 'FeatureCollection',
        features: [
          {
            type: 'Feature',
            geometry: {
              type: 'Point',
              coordinates: this.officeCoordinates,
            },
          }],
      }
    });
  }

  private async loadImages() {
    const images = [
      {
        name: 'home',
        link: 'images/home.png',
      },
      {
        name: 'bc',
        link: 'images/bc.png',
      },
      {
        name: 'bc_favorite',
        link: 'images/bc_favorite.png',
      },
      {
        name: 'drn',
        link: 'images/drn.png',
      },
    ];
    for (const image of images) {
      await this.loadImage(image.name, image.link);
    }
  }

  private loadImage(name: string,link: string) {
    return new Promise((resolve => {
      this.map.loadImage(link, (error, loadedImage) => {
        if (error) throw error;
        this.map.addImage(name, loadedImage);
        resolve();
      });
    }));
  }

  private addAddressesLayer() {
    const id = 'addresses';
    this.map.addLayer({
      'id': id,
      'type': 'symbol',
      'source': 'address',
      'layout': {
        'icon-image': 'home',
        'icon-size': 0.05
      }
    });
    this.layersIds.push({id, alias: 'Дома', isShown: true});
  }

  private addBCLayer() {
    const id = 'bc-layer';
    this.map.addLayer({
      'id': id,
      'type': 'symbol',
      'source': 'bc',
      'layout': {
        'icon-image': 'bc',
        'icon-size': 0.08
      }
    });
    this.layersIds.push({id, alias: 'Бизнес центры', isShown: true});
  }

  private addBCFavoriteLayer() {
    const id = 'bc-favorite-layer';
    this.map.addLayer({
      'id': id,
      'type': 'symbol',
      'source': 'bc_favorite',
      'layout': {
        'icon-image': 'bc_favorite',
        'icon-size': 0.05
      }
    });
    this.layersIds.push({id, alias: 'Подходящие бизнес центры', isShown: true});
  }

  private addOfficeLayer() {
    const id = 'drn-layer';
    this.map.addLayer({
      'id': id,
      'type': 'symbol',
      'source': 'drn',
      'layout': {
        'icon-image': 'drn',
        'icon-allow-overlap': true,
        'icon-size': 0.08
      }
    });
    this.layersIds.push({id, alias: 'Дорнадзор', isShown: true});
  }

  private addIsochronesLayer() {
    const id = 'isochrones-layer';
    this.map.addLayer({
      'id': id,
      'type': 'fill',
      'source': 'isochrones',
      'layout': {},
      'paint': {
        'fill-color': '#0a81f8',
        'fill-opacity': 0.2
      }
    });
    this.layersIds.push({id, alias: 'Изохроны', isShown: true});
  }

  private addHeatMap() {
    const id = 'isochrones_points-heat';
    this.map.addLayer(
        {
          'id': id,
          'type': 'fill',
          'source': 'isochrones_points',
          'layout': {},
          'maxzoom': 20,
          'paint': {
            'fill-color':[
              'interpolate',
              ['linear'],
              ["get", "NUMPOINTS"],
              1,
              "#2c83ba",
              75,
              "#addda2",
              120,
              "hsl(60, 100%, 87%)",
              180,
              "hsl(359,58%,44%)",
              234,
              "hsl(359,100%,50%)"
            ],
            'fill-opacity': 0.4,
          },
        }
    );
    this.layersIds.push({id, alias: 'Тепловая карта', isShown: true});
  }
}
